/* Define useful variables */
:root {
  --base-fontsize: 16px;

    /* Major third scale progression - see https://type-scale.com/ */
  --header-scale: 1.25;
  --line-height: 1.45;

  --body-width: 87.5%;
  --text-width: 55%;

  /* Fonts */
  --font-family: et-book, Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
  --font-sans-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  --mono-font: Consolas, Menlo, Monaco, "Andale Mono", "Ubuntu Mono", monospace;

  /* light theme */
  --text-color: #111;
  --page-background-color: #fff;
  --horizontal-rule-color: #cccccc;
  --link-selection-shadow-color: #b4d5fe;
  --article-width: 55%;
  --link-color: #b22222;
  --preformatted: #444;
  // --code: #d81b60;
}


@media screen and (-webkit-min-device-pixel-ratio: 0) {
  a:link {
    background-position-y: 87%, 87%, 87%;
  }
}

/* Adds dark mode */
@media (prefers-color-scheme: dark) {
    // :root {
    //   --text-color: #ddd;
    //   --preformatted: #ddd;
    // }

    body {
        background-color: #151515;
        color: #ddd;
    }
}

/* Tufte CSS styles */

html {
  font-size: var(--base-fontsize);
}

*,
*::before,
*::after {
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  box-sizing:border-box
}

body {
  font-family: var(--font-family);

  width: 100%;
  min-height: 100vh;
  max-width: 1440px;
  line-height: var(--line-height);

  padding-left: calc(100% - #{var(--body-width)});
  margin-left: auto;
  margin-right: auto;

  color: var(--text-color);
  background-color: var(--page-background-color);

  /* Overflow */
  overflow-x: hidden;
  word-break: break-word;
  overflow-wrap: break-word;

  /* Counter */
  counter-reset: sidenote-counter;
}

/* Fix line height when title wraps */
h1,
h2,
h3 {
  line-height: 1.1;
}

h1 {
  font-weight: 400;
  line-height: 1;
    font-size: calc(
    var(--base-fontsize) * var(--header-scale) * var(--header-scale) *
      var(--header-scale) * var(--header-scale)
  );
  margin-top: calc(var(--line-height) * 1.5rem);
}

h2 {
  font-size: calc(
    var(--base-fontsize) * var(--header-scale) * var(--header-scale) *
      var(--header-scale)
  );
  margin-top: calc(var(--line-height) * 1.5rem);

  font-style: italic;
  font-weight: 400;
}

h3 {
  font-size: calc(
    var(--base-fontsize) * var(--header-scale) * var(--header-scale)
  );
  margin-top: calc(var(--line-height) * 1.5rem);

  font-style: italic;
  font-weight: 400;
}

h4 {
  font-size: calc(var(--base-fontsize) * var(--header-scale));
  margin-top: calc(var(--line-height) * 1.5rem);
}

h5 {
  font-size: var(--base-fontsize);
  margin-top: calc(var(--line-height) * 1.5rem);
}

hr {
  display: block;
  height: 1px;
  width: var(--article-width);
  border: 0;
  border-top: 1px solid var(--horizontal-rule-color);
  margin: 1em 0;
  padding: 0;
}

article {
  position: relative;
  padding: 5rem 0rem;
  font-size: 1.4rem;

  a:link, a:visited {
    color: var(--link-color);
    font-style: italic;
  }
}

p {
  line-height: var(--line-height);
  margin: 1.4rem 0;
  padding-right: 0;
  vertical-align: baseline;
}

ul, ol {
  line-height: 2rem;
  margin: 1.4rem 0;
  padding: 0 0 0 2.5%;
  vertical-align: baseline;
}

li {
  margin-top: 1rem;
}

p, footer, table {
  width: var(--text-width);
}

/* Chapter Epigraphs */
div.epigraph {
  margin: 5em 0;

  > blockquote {
    margin-top: 3em;
    margin-bottom: 3em;
    font-style: italic;

    > {
      p {
        font-style: italic;
      }

      footer {
        font-style: normal;

        > cite {
          font-style: italic;
        }
      }
    }
  }
}

/* end chapter epigraphs styles */
blockquote {
  font-size: 1.2rem;

  p {
    margin-right: 40px;
  }

  footer {
    font-size: 1.1rem;
    text-align: right;
  }
}

figure {
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  margin: 0 0 3em 0;
  width: var(--text-width);
}

figcaption {
  float: right;
  clear: right;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.1rem;
  line-height: 1.6;
  vertical-align: baseline;
  position: relative;
  max-width: 40%;
}

figure.fullwidth figcaption {
  margin-right: 24%;
  width: var(--article-width);
}

/* Links: replicate underline that clears descenders */
a {
  &:link, &:visited {
    color: inherit;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
    background: -webkit-linear-gradient(var(--page-background-color), var(--page-background-color)), -webkit-linear-gradient(var(--page-background-color), var(--page-background-color)), -webkit-linear-gradient(#333, #333);
    background: linear-gradient(var(--page-background-color), var(--page-background-color)), linear-gradient(var(--page-background-color), var(--page-background-color)), linear-gradient(#333, #333);
    -webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
    -moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
    background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
    background-repeat: no-repeat, no-repeat, repeat-x;
    text-shadow: 0.03em 0 var(--page-background-color), -0.03em 0 var(--page-background-color), 0 0.03em var(--page-background-color), 0 -0.03em var(--page-background-color), 0.06em 0 var(--page-background-color), -0.06em 0 var(--page-background-color), 0.09em 0 var(--page-background-color), -0.09em 0 var(--page-background-color), 0.12em 0 var(--page-background-color), -0.12em 0 var(--page-background-color), 0.15em 0 var(--page-background-color), -0.15em 0 var(--page-background-color);
    background-position: 0% 93%, 100% 93%, 0% 93%;

    &::selection, &::-moz-selection {
      text-shadow: 0.03em 0 var(--link-selection-shadow-color), -0.03em 0 var(--link-selection-shadow-color), 0 0.03em var(--link-selection-shadow-color), 0 -0.03em var(--link-selection-shadow-color), 0.06em 0 var(--link-selection-shadow-color), -0.06em 0 var(--link-selection-shadow-color), 0.09em 0 var(--link-selection-shadow-color), -0.09em 0 var(--link-selection-shadow-color), 0.12em 0 var(--link-selection-shadow-color), -0.12em 0 var(--link-selection-shadow-color), 0.15em 0 var(--link-selection-shadow-color), -0.15em 0 var(--link-selection-shadow-color);
      background: var(--link-selection-shadow-color);
    }
  }
}

img {
  max-width: 100%;
}

blockquote {
  .sidenote, .marginnote {
    margin-right: -82%;
    min-width: 59%;
    text-align: left;
  }
}

div.fullwidth, table.fullwidth, figure.fullwidth {
  width: 100%;
  clear: both;
}

div.table-wrapper {
  overflow-x: auto;
  font-family: "Trebuchet MS", "Gill Sans", "Gill Sans MT", sans-serif;
}


/* Use mono font for code like elements */
code,
pre,
pre span,
kbd,
samp {
  font-size: .9rem;
  font-family: var(--mono-font);
  color: var(--code);
}

kbd {
  color: var(--preformatted);
  border: 1px solid var(--preformatted);
  border-bottom: 3px solid var(--preformatted);
  border-radius: 5px;
  padding: 0.1rem;
}

pre {
  padding: 1rem 1.4rem;
  max-width: 100%;
  overflow: auto;
  overflow-x: auto;
  color: var(--preformatted);
  background: var(--accent-bg);
  border: 1px solid var(--border);
  border-radius: 5px;
}

/* Fix embedded code within pre */
pre code {
  color: var(--preformatted);
  background: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  code {
    font-size: calc(var(--base-fontsize) * 1.2);
  }
}

/* Custom class */
.numeral {
  font-family: et-book-roman-old-style;
}

.danger {
  color: red;
}

.subtitle {
  font-size: calc(
    var(--base-fontsize) * var(--header-scale) * var(--header-scale)
  );
  font-style: italic;
  margin: 1rem 0;
  display: block;
  line-height: 1;
}

.sidenote, .marginnote {
  float: right;
  clear: right;
  color: #667;
  margin-right: -60%;
  width: 50%;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.1rem;
  line-height: 1.3;
  vertical-align: baseline;
  position: relative;
  padding: 1%;
  border-radius: var(--base-fontsize);
  background-color: #a1a1;

}

.sidenote-number {
  counter-increment: sidenote-counter;

  &:after {
    content: counter(sidenote-counter);
    font-family: et-book-roman-old-style;
    position: relative;
    vertical-align: baseline;
    font-size: 1rem;
    top: -0.5rem;
    left: 0.1rem;
  }
}

.sidenote:before {
  content: counter(sidenote-counter) " ";
  font-family: et-book-roman-old-style;
  position: relative;
  vertical-align: baseline;
  top: -0.5rem;
}

.sans {
  font-family: var(--font-sans-family);
  letter-spacing: .03em;
}

.sans > code {
  font-size: 1.2rem;
}

span.newthought {
  font-variant: small-caps;
  font-size: 1.2em;
}

input.margin-toggle {
  display: none;
}

label {
  &.sidenote-number {
    display: inline;
  }

  &.margin-toggle:not(.sidenote-number) {
    display: none;
  }
}
