@import 'tufte';

/* Use a responsive layout for smaller screens */
@media (max-width: 1024px) {
  /* Define useful variables */
  :root {
    --body-width: 95%;
    --article-width: 75%;
    --text-width: 70%;
    --base-fontsize: 14px;
  }

  article {
    padding: 1rem 0;
  }

  figure {
    max-width: 95%;
    text-align: center;
  }

  figcaption, figure.fullwidth figcaption {
    margin-right: 0%;
    max-width: none;
  }

  blockquote {
    margin-left: 1.5em;
    margin-right: 0em;
  }

  label.margin-toggle:not(.sidenote-number) {
    display: inline;
    font-size: 1.5rem;
  }

  .sidenote, .marginnote {
    display: none;
  }

  .margin-toggle:checked + {
    .sidenote, .marginnote {
      display: block;
      float: left;
      left: 1rem;
      clear: both;
      width: 95%;
      margin: 1rem 2.5%;
      position: relative;
    }
  }

  label {
    cursor: pointer;
  }

  div.table-wrapper, table {
    width: 95%;
  }

  img {
    width: 95%;
  }
}

@media (max-width: 512px) {
  :root {
    --body-width: 95%;
    --article-width: 100%;
    --text-width: 100%;
    --base-fontsize: 12px;
  }

  article {
    ol, ul {
      -webkit-padding-start: 5%;
      li > p {
        width: 100%;
      }
    }
  }
}

/* Use sans-serif for headings, list-items etc. */
h1, h2, h3, h4, h5, .c-banner, .list-item, time, p.subtitle {
  font-family: var(--font-sans-family);
  font-display: swap;
}

/* Tufte CSS applies top and bottom padding to <section> tags, but we cannot use
 * sections very easily with Hugo's Markdown renderer. Instead, we can apply the
 * equivalent padding to any <h2> tags, as those are explicitly used for section
 * headings in Tufte CSS. It's not pixel perfect with the example given in Tufte
 * CSS, but it's close.*/
h2:not(header) { padding-top: 2rem; }

/* Tufte CSS uses <pre class="code">, but Markdown generates <pre><code>. We can
 * fix part of that by using the same styles there, but the font-family used for
 * the <code> is still different than the one in Tufte CSS. */
.highlight, pre code {
  overflow-x: auto;
}

sup {
  vertical-align: top;
  position: relative;
  top: -0.5em;
}
// Reduce Tufte CSS's article padding
article {
  padding: 3rem 0 !important;

  header {
    margin-bottom: 2rem;
  }

}

/* Tufte CSS relies on sections. Since I'm not doing that, move those styles
 * to relevant selectors. */
article {
  ol, ul {
    width: var(--text-width);
    -webkit-padding-start: 5%;
    li > p {
      width: 100%;
    }
  }
}

// Add slight margin to figure top
figure {
  margin-top: 1.5rem;
}

/* Hyphenate content -- http://clagnut.com/blog/2395/ */
p {
  -webkit-hyphens: auto;
  -webkit-hyphenate-limit-chars: 12 6 5;
  -webkit-hyphenate-limit-lines: 1;
  -webkit-hyphenate-limit-last: always;
  -webkit-hyphenate-limit-zone: 15%;
  -moz-hyphens: auto;
  -moz-hyphenate-limit-chars: 12 6 5;
  -moz-hyphenate-limit-lines: 1;
  -moz-hyphenate-limit-last: always;
  -moz-hyphenate-limit-zone: 15%;
  -ms-hyphens: auto;
  -ms-hyphenate-limit-chars: 12 6 5;
  -ms-hyphenate-limit-lines: 1;
  -ms-hyphenate-limit-last: always;
  -ms-hyphenate-limit-zone: 15%;
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

section {
  header > h2, header > p {
    margin-bottom: 0;
    margin-top: .5rem;
  }
}

// If side/margin notes are very close, they start sticking. Add some margin.
.sidenote, .marginnote {
  margin-top: 1rem;
}

.sidenote-number, .margin-toggle {
  color: var(--link-color);
}

/* Banner/Heading/Nav-bars */
.c-banner  {
  .c-banner-title {
    font-size: 3rem;
    letter-spacing: 0.2rem;
    margin-top: 2rem;
  }
  .c-nav-bar {
    font-size: 1.75rem;
    font-variant: small-caps;
    margin-top: 3rem;
    letter-spacing: 0.1rem;

    a {
      margin: 0 4px 0 0;
      text-decoration: none;
    }
  }
  .c-banner-subclause {
    font-size: 1.6rem;
  }
}

#TableOfContents {
  max-width: 30ch;
  font-size: 95%;
  float: left;
  position: relative;
  border: 1px solid var(--horizontal-rule-color);
  box-sizing: content-box;
  margin: 0rem 1rem 1.5rem 0;
  padding: 10px 10px 2px 14px;
  line-height: 1.25;

  ul {
    width: 100%;
    counter-reset: toc;
    list-style-type: none;
    padding: 0;
    text-indent: 0;
    margin-top:4px;
    ul {
      list-style-type:none;
      padding-left:0.7em;
      margin-top:2px
    }
  }

  li {
    list-style-position: inside;
    font-weight: bold;
    margin: 5px 0 10px 0;
    position: relative;
    overflow-wrap: break-word;

    &::before {
      counter-increment: toc;
      content: counters(toc, ".") ". ";
    }

    li {
      margin-bottom: 0;
      font-weight: normal;
      font-size: 0.9em;
    }
  }

  a {
    color: var(--text-color);
  }
}

.list-item {
  margin-top: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  x-justify-content: space-around;
  x-align-items: stretch;

  .meta {
    width: 150px;
    flex: 0 1 150px;
    font-size: 1.3rem;
    align-self: center;
  }

  .li-details {
    display: flex;
    flex-direction: column;
    margin: 5px 0;
    padding-left: 3rem;
    border-left: 1px solid #333;

    .list-title a {
      font-size: 1.5rem;
      font-style: italic;
      color: var(--text-color);
    }
    .list-title-subtitle {
      font-size: 1.4rem;
    }

  }
}

.footnotes {
  ol, ul {
    width: var(--article-width);
    box-sizing: content-box;
  }

  ol li {
    font-size: 1.1rem;
    line-height: 1.3;
  }

  a[href^="#fnref:"] {
    font-size: 1.1rem;
    color: var(--link-color);
  }
}

.hint {
  display: inherit;
  font-size: 1.2rem;
  // width: var(--text-width);
}

.note-hint {
  font-variant: small-caps;
  font-weight: bold;
  font-size: 1.1em;
}

a.footnote-ref {
  font-family: et-book-roman-old-style;
  color: var(--link-color);
}

.epigraph {
  footer {
    color: var(--link-color);
  }
}

a.read-more {
  font-style: italic;
}

